import axios from "axios";
export class TpService{
    baseUrl = "http://localhost:8080/api/TipoProducto";
    getAll(){
        return axios.get(this.baseUrl+"/getTipoProductos").then (res => res.data);   
    }

    save(tipoproductos) {
        return axios.post(this.baseUrl + "/Agregar", tipoproductos).then (res => res.data);   
    }
    delete(id){
        return axios.delete(this.baseUrl + "/delete/"+id).then(res => res.data);   
    }
}
