import React, {Component} from 'react';
import './App.css';
import { TpService } from './service/TpService';
import {DataTable} from 'primereact/datatable';
import { Column } from 'primereact/column';
import {Panel} from 'primereact/panel';
import {Menubar} from 'primereact/menubar';
import { Dialog } from 'primereact/dialog';
import { InputText } from 'primereact/inputtext';
import { Button } from 'primereact/button';
import { Toast } from 'primereact/toast';



import "primereact/resources/themes/lara-light-indigo/theme.css";
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";


export default class App extends Component{
  constructor(){
    super();
    this.state = {
      visible : false,
      tipoproductos : {
        id: null,
        referencia: null,
        nombre : null,
        color : null,
        edad : null,
        nivel_azucar : null,
        cepa : null,
        crianza : null,
        metodo_elaboracion : null
      },
      selectproducto : {

      }
    };
    this.items = [
      {
        label: 'Nuevo',
        icon: 'pi pi-fw pi-plus',
        command: () => {this.showDialog()}
      },
      {
        label: 'Editar',
        icon: 'pi pi-fw pi-pencil',
        command: () => {this.showEditDialog ()}
      },
      {
        label: 'Eliminar',
        icon: 'pi pi-fw pi-trash',
        command: () => {this.delete()}
      }
    ]; 
    this.tpService = new TpService();
    this.save = this.save.bind(this); 
    this.delete = this.delete.bind(this);

    this.footer = (
      <div>
        <Button label = "Guardar" icon = "pi pi-check" onClick={this.save}/>
      </div>
    );

  
  }

  componentDidMount(){
    this.tpService.getAll().then(data => this.setState({tipoproducto: data}))
  }

  save() {
    this.tpService.save(this.state.tipoproductos).then(data => {
     this.setState({
       visible : false,
       tipoproductos : {
        id: null,
        referencia: null,
        nombre : null,
        color : null,
        edad : null,
        nivel_azucar : null,
        cepa : null,
        crianza : null,
        metodo_elaboracion : null
      }
     });
      Toast.current.show({severity: 'success', summary: 'Atencion', detail: 'Guardado con exito'});
       this.tpService.getAll().then(data => this.setState({tipoproducto: data}))

    })
  }

  delete(){
    if (window.confirm("¿Realmete deseas eliminar el registro seleccionado?")){
      this.tpService.delete(this.state.selectproducto.id).then(data => {
        Toast.current.show({severity: 'success', summary: 'Atencion', detail: 'Se ha eliminado el registro con exito'});
        this.tpService.getAll().then(data => this.setState({tipoproducto: data})); 
      });
    }
  }

  render(){
    return (
      <div style={{width:'80%', margin: '0 auto', marginTop: '40px'}}>
     <Menubar model = {this.items}/>
     <br/>
     <Panel header = "React CRUD App" >
      <DataTable value={this.state.tipoproducto} selectionMode ="single" selection={this.state.selectproducto} onSelectionChange ={e => this.setState({selectproducto: e.value})}>
        <Column field='id' header='ID '></Column>
        <Column field='referencia' header='Referencia '></Column>
        <Column field='nombre' header='Nombre '></Column>
        <Column field='color' header='Color '></Column>
        <Column field='edad' header='Edad '></Column>
        <Column field='Nivelazucar' header='Nivel de  azucar '></Column>
        <Column field='cepa' header='Cepa '></Column>
        <Column field='crianza' header='Crianza '></Column>
        <Column field='MetodoElaboracion' header='Metodo de elaboracion '></Column>
      </DataTable>
     </Panel>


     
     <Dialog header="Crear producto" visible={this.state.visible} style={{ width: '400px' }} footer={this.footer} modal={true} onHide={() => this.setState({visible: false})}>
     <form id= 'persona-form'>
     <span className="p-float-label">
      <InputText style = {{width : '100%'}} value={this.state.value} id="referencia" onChange={(e) => 
      {
        let val = e.target.value;
        this.setState(prevState => {
          let tipoproductos = Object.assign({}, prevState.tipoproductos)
        tipoproductos.referencia = val

        return {tipoproductos};
      })}} />
      <label htmlFor="referencia">referencia</label>

      </span> <br/>

      <span className="p-float-label">
      <InputText style = {{width : '100%'}}  value={this.state.value} id="Nombre" onChange={(e) =>{
        let val = e.target.value;
        this.setState(prevState => {
          let tipoproductos = Object.assign({}, prevState.tipoproductos)
        tipoproductos.Nombre = val

        return {tipoproductos};
      })}} />
          <label htmlFor="Nombre">Nombre</label>

      </span><br/>

      <span className="p-float-label">
      <InputText style = {{width : '100%'}}  value={this.state.value} id="color" onChange={(e) => 
      {
        let val = e.target.value;
        this.setState(prevState => {
          let tipoproductos = Object.assign({}, prevState.tipoproductos)
        tipoproductos.color = val

        return {tipoproductos};
      })}} /> 
  <label htmlFor="color">color</label>

      </span><br/>

      <span className="p-float-label">
      <InputText style = {{width : '100%'}}  value={this.state.value} id="edad" onChange={(e) => 
      {
        let val = e.target.value;
        this.setState(prevState => {
          let tipoproductos = Object.assign({}, prevState.tipoproductos)
        tipoproductos.edad = val

        return {tipoproductos};
      })}} />
          <label htmlFor="edad">edad</label>

      </span><br/>

      <span className="p-float-label">
      <InputText style = {{width : '100%'}}  value={this.state.value} id="Nivelazucar" onChange={(e) => 
      {
        let val = e.target.value;
        this.setState(prevState => {
          let tipoproductos = Object.assign({}, prevState.tipoproductos)
        tipoproductos.Nivelazucar = val

        return {tipoproductos};
      })}} />
          <label htmlFor="Nivelazucar">Nivel de azucar</label>

      </span><br/>
      
      <span className="p-float-label">
      <InputText style = {{width : '100%'}}  value={this.state.value} id="cepa" onChange={(e) => 
       {
        let val = e.target.value;
        this.setState(prevState => {
          let tipoproductos = Object.assign({}, prevState.tipoproductos)
        tipoproductos.cepa = val

        return {tipoproductos};
      })}} />
          <label htmlFor="cepa">cepa</label>

      </span><br/>

      <span className="p-float-label">
      <InputText style = {{width : '100%'}}  value={this.state.value} id="crianza" onChange={(e) => 
       {
        let val = e.target.value;
        this.setState(prevState => {
          let tipoproductos = Object.assign({}, prevState.tipoproductos)
        tipoproductos.crianza = val

        return {tipoproductos};
      })}} />
          <label htmlFor="crianza">crianza</label>

      </span><br/>

      <span className="p-float-label">
      <InputText style = {{width : '100%'}}  value={this.state.value} id="MetodoElaboracion" onChange={(e) => 
       {
        let val = e.target.value;
        this.setState(prevState => {
          let tipoproductos = Object.assign({}, prevState.tipoproductos)
        tipoproductos.MetodoElaboracion = val

        return {tipoproductos};
      })}} />
          <label htmlFor="MetodoElaboracion">Metodo de elaboracion</label>

      </span><br/>
      </form>
     </Dialog>
     
     <Toast ref={Toast} />


    </div>
    );
  }
  showDialog(){
    this.setState({
      visible: true,
      tipoproductos : {
        id: null,
        referencia: null,
        nombre : null,
        color : null,
        edad : null,
        nivel_azucar : null,
        cepa : null,
        crianza : null,
        metodo_elaboracion : null
      }
    
    });
    document.getElementById('persona-form').reset();
  }
  showEditDialog() {
    this.setState({
      visible: true,
      tipoproductos : {
        id: this.state.selectproducto.id,
        referencia: this.state.selectproducto.referencia,
        nombre : this.state.selectproducto.Nombre,
        color : this.state.selectproducto.color,
        edad : this.state.selectproducto.edad,
        nivel_azucar : this.state.selectproducto.Nivelazucar,
        cepa : this.state.selectproducto.cepa,
        crianza : this.state.selectproducto.crianza,
        metodo_elaboracion : this.state.selectproducto.MetodoElaboracion
      }
    });
  }
}


        
        